import { Vector3, Object3D } from 'three'

export class DampingMovementController {
    damp: number
    attract: number
    targetPos: Vector3
    currentPos: Vector3
    object: Object3D
    velocity: Vector3
    childObjects: Array<Object3D>
    constructor(object: Object3D, damp: number = 0.5, attract: number = 0.5) {
        this.damp = damp
        this.attract = attract
        this.targetPos = new Vector3().copy(object.position)
        this.currentPos = new Vector3().copy(object.position)
        this.object = object
        this.velocity = new Vector3()
        this.childObjects = []
    }
    addChildObj(object: Object3D) {
        this.childObjects.push(object)
    }
    update(target: Vector3) {
        this.velocity.multiplyScalar(this.damp)
        let currentPath = target.sub(this.object.position)
        this.velocity.add(currentPath.multiplyScalar(this.attract))
        this.object.position.add(this.velocity)
        this.childObjects.forEach(obj => {
            obj.position.copy(this.object.position)
            obj.rotation.copy(this.object.rotation)
        })
    }
}